#courses {
  padding: 3rem 15%;
}

#courses .container {
  display: flex;
  flex-direction: row;
  padding: 3rem 0;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.course-card {
  border: 3px solid #000;
  width: 28%;
}

.course-card img {
  width: 100%;
  border-bottom: 3px solid #000;
}

.course-card .card-body {
  padding: 1rem;
}

.course-card h1 {
  font-size: 2rem;
}

.course-card p {
  font-size: 1.2rem;
  margin-top: 0.4rem;
}

.course-card button {
  border: 3px solid #f24e1e;
  color: #f24e1e;
  font-size: 1.2rem;
  display: block;
  margin: 1rem auto 0 auto;
  font-weight: 700;
  padding: 0.4rem 0.6rem;
  position: relative;
}

.course-card button:hover {
  color: #fff;
}

.course-card button::before {
  transition: all 0.2s ease;
  content: '';
  width: 0;
  height: 100%;
  background: #f24e1e;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: -1;
}

.course-card button:hover::before {
  width: 100%;
}

.switch-all {
  background: #f24e1e;
  color: #fff;
  padding: 1rem 1.4rem;
  font-size: 1.8rem;
  display: block;
  margin: auto;
  position: relative;
  top: 0;
  bottom: unset;
}

.switch-all::before {
  transition: all 0.2s ease;
  content: '';
  width: 100%;
  height: 0;
  background: rgba(0, 0, 0, 0.1);
  position: absolute;
  top: inherit;
  bottom: inherit;
  left: 0;
}

.switch-all:hover::before {
  height: 100%;
}

.container.disable {
  transition: all 0.6s ease;
  width: 100%;
  height: 0;
  opacity: 0;
}
